.LUArea { max-height: 1080px; }

.LUArea .LUWrap { position: relative; height: calc(100% - 100px); width: 100%; padding-top: 100px; }
.LUArea .LUWrap .h2Area { position: relative; margin: 40px 0; z-index: 996; }
.LUArea .LUWrap .h2Area h2 { margin: 0; position: relative; text-align: center; width: 100%; }

.LUArea .LUWrap .itemArea { 
    width: 100%; height: calc(100% - 200px); position: relative; 
    display: inline-flex; justify-content: center; align-items: center; flex-direction: row;
}

.LUArea .LUWrap .itemArea .lineups .lineup { margin: 16px; height: fit-content; }
.LUArea .LUWrap .itemArea .lineups .lineup h1 { width: 250px; text-transform: none; font-size: 40px; line-height: 56px; background: url(../../public/assets/img/04_LINEUP_board.jpg) no-repeat; background-position: center; background-size: 100% 100%; text-align: center; z-index: 997; }